import { Button } from '@material-ui/core';
import MaterialTable from 'material-table';
import React, { Component } from 'react';
import { deleteData, createData } from '../../Helper/ActionGlobal';

export default class CTable extends Component {
   constructor(props) {
      super(props);

      this.state = {
         listAllData: false,
         attachment: [],
         showButtonFilesUploadAdd: true
      }
   }

   async _handlerPostData(newRowDataInput) {

      const postDataBody = {
         url_to_image: newRowDataInput[1],
         product_name: newRowDataInput[0].product_name,
         purchase_price: newRowDataInput[0].purchase_price,
         selling_price: newRowDataInput[0].selling_price,
         stock: newRowDataInput[0].stock
      }

      const postTicketBodyArray = Object.values(postDataBody);
      const result = await createData(...postTicketBodyArray);

      if (result.status === 200) {

         this.setState({
            showButtonFilesUploadAdd: true,
            attachment: []
         })
         alert("\nProduct data saved successfully!")

      } else {

         if (result.data.status.messages.user) {
            if (result.data.status.messages.user.product_name) {

               this.setState({
                  showButtonFilesUploadAdd: true,
                  attachment: []
               })
               alert(`\nProduct data failed to upload!\n${result.data.status.messages.user.product_name}`)

               return
            }

            this.setState({
               showButtonFilesUploadAdd: true,
               attachment: []
            })
            alert(`\nProduct data failed to upload!\n${result.data.status.messages.user}`)

         }
      }
   }

   async _handlerPutData(updateRowDataInput) {

      // const putDataBody = {
      //    url_to_image: updateRowDataInput[1],
      //    product_name: updateRowDataInput[0].product_name,
      //    purchase_price: updateRowDataInput[0].purchase_price,
      //    selling_price: updateRowDataInput[0].selling_price,
      //    stock: updateRowDataInput[0].stock
      // }

      // const putTicketBodyArray = Object.values(putDataBody);
      // const result = await update(...putTicketBodyArray);

      // if (result.status === 200) {

      //    this.setState({
      //       showButtonFilesUploadAdd: false,
      //       attachment: []
      //    })
      //    alert("\nProduct data updated successfully!")

      // } else {

      //    if (result.data.status.messages.user) {
      //       if (result.data.status.messages.user.product_name) {

      //          this.setState({
      //             showButtonFilesUploadAdd: false,
      //             attachment: []
      //          })
      //          alert(`\nProduct data failed to upload!\n${result.data.status.messages.user.product_name}`)

      //          return
      //       }

      //       this.setState({
      //          showButtonFilesUploadAdd: false,
      //          attachment: []
      //       })
      //       alert(`\nProduct data failed to upload!\n${result.data.status.messages.user}`)

      //    }
      // }
   }

   async _handlerDeleteDataById(id) {
      const result = await deleteData(id);

      if (result.status === 200) {
         alert("\nProduct data deleted successfully!")
      }
   }

   render() {

      const { listAllData, attachment, showButtonFilesUploadAdd } = this.state;
      const tableRef = React.createRef();

      const columns = [
         {
            title: 'Product Image',
            field: 'url_to_image',
            filtering: false,
            editComponent: (rowData) => (

               <div>
                  {
                     showButtonFilesUploadAdd &&
                     <Button
                        variant="contained"
                        component="label"
                        style={{ fontSize: 9 }}
                     >
                        {"Upload Foto"}
                        <input type="file" style={{ display: "none" }} id="input" accept=".jpg, .png"
                           onChange={(e) => {
                              const fl = e.target.files[0];
                              const newAttachment = [...attachment]

                              // console.log("fl", fl);
                              this.setState({ showButtonFilesUploadAdd: false })

                              fl && newAttachment.push(fl)
                              this.setState({ attachment: newAttachment[0] })
                           }}
                        />
                     </Button>
                  }
                  {
                     !showButtonFilesUploadAdd &&
                     <img
                        src={"https://rahmatsaputra.my.id/public/upload_success.png"}
                        alt=""
                        style={{
                           width: 130,
                           height: 80
                        }}

                     />
                  }
               </div>
            ),
            render: (rowData) =>
               <img
                  src={rowData.url_to_image ? rowData.url_to_image : "data:image/png;base64,"}
                  alt=""
                  style={{
                     width: rowData.url_to_image ? 100 : 100,
                     height: rowData.url_to_image ? 100 : 100,
                     marginLeft: rowData.url_to_image ? 8 : 0
                  }}
               />
         },
         {
            title: 'Product Name',
            field: 'product_name',
            validate: rowData => {
               if (rowData.product_name === undefined || rowData.product_name === '') {
                  return 'Required';
               } else if (rowData.product_name.length < 3) {
                  return 'Product name should contain  at least  3 char';
               }
               return true
            }
         },
         {
            title: 'Purchase Price',
            field: 'purchase_price',
            type: 'numeric',
            validate: rowData => {
               if (rowData.purchase_price === undefined || rowData.purchase_price === '') {
                  return 'Required';
               }
               return true
            }
         },
         {
            title: 'Selling Price',
            field: 'selling_price',
            type: 'numeric',
            validate: rowData => {
               if (rowData.selling_price === undefined || rowData.selling_price === '') {
                  return 'Required';
               }
               return true
            }
         },
         {
            title: 'Stock',
            field: 'stock',
            type: 'numeric',
            validate: rowData => {
               if (rowData.stock === undefined || rowData.stock === '') {
                  return 'Required';
               }
               return true
            }
         }
      ];

      return (
         <div>
            <MaterialTable
               title="Product Table"
               tableRef={tableRef}
               data={query =>
                  new Promise((resolve, reject) => {

                     if (query.search.length > 0) {
                        let url = 'https://staging.rahmatsaputra.my.id/v0/nutech/product?'
                        url += 'q=' + query?.search

                        fetch(url)
                           .then(response => response.json())
                           .then(result => {

                              if (result.meta.http_status_code == 200) {
                                 this.setState({
                                    listAllData: result.data
                                 })
                                 resolve({
                                    data: result.data,
                                    page: result.current_page - 1,
                                    totalCount: result.total
                                 })
                              }

                              resolve()
                              return true
                           })

                     } else {

                        let url = 'https://staging.rahmatsaputra.my.id/v0/nutech/product?'
                        url += 'per_page=' + query.pageSize
                        url += '&page=' + (query.page + 1)

                        fetch(url)
                           .then(response => response.json())
                           .then(result => {

                              this.setState({
                                 listAllData: result.data
                              })

                              resolve({
                                 data: result.data,
                                 page: result.current_page - 1,
                                 totalCount: result.total
                              })
                           })
                     }
                  })
               }
               columns={columns}
               options={{
                  paging: false,
                  actionsColumnIndex: -1,
                  addRowPosition: 'first'
               }}
               editable={{
                  onRowAdd: (newRow) => new Promise((resolve, reject) => {
                     setTimeout(() => {
                        const newRowDataInput = [newRow];

                        newRowDataInput.push(attachment);
                        { this._handlerPostData(newRowDataInput) }

                        resolve();
                        tableRef.current && tableRef.current.onQueryChange();

                     }, 500);
                  }),
                  onRowDelete: (selectedRow) => new Promise((resolve, reject) => {
                     const index = selectedRow.tableData.id;
                     const updatedRows = [...listAllData];
                     const selectedId = updatedRows[index].id;

                     setTimeout(() => {
                        { this._handlerDeleteDataById(selectedId) }

                        resolve();
                        tableRef.current && tableRef.current.onQueryChange();

                     }, 500);
                  }),
                  onRowUpdate: (updatedRow, oldRow) => new Promise((resolve, reject) => {
                     //looking for id
                     const index = oldRow.tableData.id;
                     const updatedRows = [...listAllData];
                     const selectedId = updatedRows[index].id;

                     // console.log("index", selectedId);
                     // console.log("updatedRow", updatedRow);


                     // filter mandatory field from updatedRow
                     const asArray = Object.entries(updatedRow);

                     const filteringData = asArray.filter(
                        ([key, value]) =>
                           // key == "url_to_image" ||
                           key == "product_name" ||
                           key == "purchase_price" ||
                           key == "selling_price" ||
                           key == "stock"
                     )

                     const asObject = Object.fromEntries(filteringData);

                     // console.log("result", asObject);



                     // if (!asObject.url_to_image) {

                     //    const newRowDataInput = [updatedRow];
                     //    newRowDataInput.push(attachment);

                     //    console.log("newRowDataInput", newRowDataInput);
                     //    console.log("attachmentUpdate", attachment);

                     // }



                     setTimeout(() => {
                        const updateRowDataInput = [asObject];

                        updateRowDataInput.push(attachment);
                        console.log("updateRowDataInput", updateRowDataInput);
                        // { this._handlerPutData(updateRowDataInput) }

                        resolve();
                        tableRef.current && tableRef.current.onQueryChange();

                     }, 500);
                     // console.log("onRowUpdate", updatedRows);
                  })
               }}
            />
         </div >
      );
   }
}